<template>
  <div class="vueI18n">
    <div class="container">
      <a  @click="changeLang('cn')"><img :class="{'active':currentLang==='cn'}" src="./flag-cn.png" alt="" srcset="" width="20px"></a> |
      <a  @click="changeLang('en')"><img :class="{'active':currentLang==='en'}" src="./flag-uk.png" alt="" srcset="" width="20px"></a> |
      <a @click="changeLang('thai')"><img :class="{'active':currentLang==='thai'}" src="./flag-th.png" alt="" srcset="" width="20px"></a> |
      <a @click="changeLang('vietnam')"><img :class="{'active':currentLang==='vietnam'}" src="./flag-vn.png" alt="" srcset="" width="20px"></a> |
      <a @click="changeLang('melayu')"><img :class="{'active':currentLang==='melayu'}" src="./flag-my.png" alt="" srcset="" width="20px"></a>
    </div>
  </div>
</template>

<script>
export default {
  name: 'vueI18n',
  props: {
    show: Boolean
  },
  data () {
    return {
      currentLang: localStorage.getItem('language') ? localStorage.getItem('language') : 'cn'
    }
  },
  methods: {
    changeLang (lang) {
      this.$emit('click', false)
      this.$i18n.locale = lang
      this.setLocalStorage(lang)
      this.activeLang(lang)
    },
    activeLang (lang) {
      this.currentLang = localStorage.getItem('language')
    },
    setLocalStorage (lang) {
      localStorage.setItem('language', lang)
    }
  }
}
</script>

